.tasks{
    list-style: none;
    margin: 0;
    padding: 0;
}
.tasks__item{
    display: grid;
    grid-template-columns:auto 1fr auto;
    grid-gap: 10px;
    align-items: center;

    padding:10px;
}
.tasks__button--toggleDone{
    background-color: hsl(120, 61%, 35%);
}
.tasks__button--toggleDone:hover {
    background: hsl(120, 61%, 39%);
}
.tasks__button--toggleDone:active{
    background: hsl(120, 61%, 45%);
}
.tasks__button{
    border:none;
    color: white;
    width: 30px;
    height: 30px;
    padding: 0;
    transition: background 0.3s;
}
.tasks__item--hidden {
    display: none;
}
.tasks__content--done{
    text-decoration: line-through;
}
.tasks__button--remove{
    background: hsl(348, 83%, 47%);
}
.tasks__button--remove:hover{
    background: hsl(348, 83%, 52%);
}
.tasks__button--remove{
    background: hsl(348, 83%, 57%);
}
